'use strict';
const angular = require('angular');
import _ from 'lodash';

/*@ngInject*/
export function groupFilter($rootScope) {
	return function(items) {
		
		return _.memoize(function(items, field) {
		    return _.transform(_.groupBy(items, 'order'), (result, value, key) => {
		      result.push({
		        order: key,
		        contacts: value
		      });
		    }, []);
		}, function resolver(items, field) {
		    return items.length + field;
		});

	}
}

export default angular.module('abcContactsApp.groupFilter', [])
  .filter('groupFilter', groupFilter)
  .name;



