'use strict';
const angular = require('angular');
const uiRouter = require('angular-ui-router');
import async from 'async';

import routes from './test.routes';

export class TestComponent {
  /*@ngInject*/
  constructor($q, $http, $state, $scope, $timeout, $rootScope) {
    this.$http = $http;
    this.$scope = $scope;
    this.$timeout = $timeout;
    this.$rootScope = $rootScope;
    
    if (!$rootScope._isLogin){
      return $state.go('auth', { from: 'test' });
    }

    this.tasks = [
      { 
        caption: 'POST /api/auth (token)', 
        url: `${this.$rootScope.__basePath}/api/auth`,
        status: 'working',
        type: 'POST'
      },
      { 
        caption: 'GET /api/agenda (todos los contactos)', 
        url: `${this.$rootScope.__basePath}/api/agenda`,
        status: 'working'
      },
      { 
        caption: 'GET /api/agenda/suggest (todos los contactos sugeridos)', 
        url: `${this.$rootScope.__basePath}/api/agenda/suggest`,
        status: 'working'
      },
      { 
        caption: 'GET /api/me (informacin del user actual)', 
        url: `${this.$rootScope.__basePath}/api/me`,
        status: 'working',
        type: 'POST'
      },
    ];
  }

  $onInit(){
    var _then = (task, res) => {
      task.status = 'success';
      console.log('success');
      //this.$scope.$apply();
      this.$timeout(() => {
        this.$scope.$apply();
      });
    };

    var _catch = (task, err) => {
      task.status = 'error';
      console.log('error');
      this.$timeout(() => {
        this.$scope.$apply();
      });
      //this.$scope.$apply();
    };

    async.each(
      this.tasks,
      (task, cb) => {
        if (task.type === 'POST'){
          this.$http
              .post(task.url)
              .then(res => {
                _then(task, res);
                cb();
              })
              .catch(err => {
                _catch(task, err);
                cb();
              });
        } else {
          this.$http
              .get(task.url)
              .then(res => {
                _then(task, res);
                cb();
              })
              .catch(err => {
                _catch(task, err);
                cb();
              });
        }
      },
      (err) => {
        if (err){
          console.log(err)  
        }
      }
    );
  }
}

export default angular.module('abcContactsApp.test', [uiRouter])
  .config(routes)
  .component('test', {
    template: require('./test.html'),
    controller: TestComponent
  })
  .name;



