'use strict';
const angular = require('angular');
import _ from 'lodash';

/*@ngInject*/
export function contactFilter($rootScope) {
	return function(items) {

		if (!$rootScope.__filter)
			return items;

		let sitems = $rootScope.__filter.result;

		var filtered = _.filter(items, i => {	
			return _.find(sitems, s => {
				return s.uid === i.uid;
			}) !== undefined;
		});

		return filtered;
	}
}

export default angular.module('abcContactsApp.contactFilter', [])
  .filter('contactFilter', contactFilter)
  .name;



