'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './logout.routes';

export class LogoutComponent {
  /*@ngInject*/
  constructor($rootScope, $timeout, $http) {
    this.$rootScope = $rootScope;
    this.$timeout = $timeout;
    this.$http = $http;
  }

  $onInit() {
    this.$http
        .post(`${this.$rootScope.__basePath}/api/logout`)
        .then( res => {
          let data = res.data;
          this.$rootScope.__token = undefined;
          this.$timeout(() => {
            window.location.href = data.redirect;
          }, 500)
        })
        .catch(err => {
          console.log(err);
          toastr.error(err.statusText);
        });
  }
}

export default angular.module('abcContactsApp.logout', [uiRouter])
  .config(routes)
  .component('logout', {
    template: require('./logout.html'),
    controller: LogoutComponent,
    controllerAs: 'logoutCtrl'
  })
  .name;



