'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import toastr from 'toastr';

import routes from './sugeridos.routes';

export class SugeridosComponent {
  /*@ngInject*/
  constructor($scope, $http, $rootScope, $state, agendaService, $mdToast, $mdDialog, $timeout) {
    if (!$rootScope._isLogin){
      return $state.go('auth', { from: 'sugeridos' });
    }

    this.$http = $http;
    this.agendaService = agendaService;
    this.$mdToast = $mdToast;
    this.$rootScope = $rootScope;
    this.$mdDialog = $mdDialog;

    var last = {
      bottom: false,
      top: true,
      left: false,
      right: true
    };

    this.toastPosition = angular.extend({},last);

    $rootScope.$watch(() => { return $rootScope.__suggests; }, (value) => {
      if (value)
        this.groups = value.grouped;
    });

    $rootScope.$on('filterChange', () => {
      let value = $rootScope.__filter;
      
      if (!$rootScope.__suggests)
        return;

      if (!value) {
        this.groups = $rootScope.__suggests.grouped;
      } else {
        let sitems = value.result;

        var filtered = _.filter($rootScope.__suggests.contacts, i => { 
          return _.find(sitems, s => {
            return s.uid === i.uid;
          }) !== undefined;
        });

        this.groups = _.transform(_.groupBy(filtered, 'order'), (result, value, key) => {
          result.push({
            order: key,
            contacts: value
          });
        }, []);

        $timeout(() => {
          $scope.$apply();
        }, 200);
      }
    });

    $rootScope.$watch(() => { return $rootScope.__filter; }, (value) => {
      return;
      if (!$rootScope.__suggests)
        return;

      if (!value) {
        this.groups = $rootScope.__suggests.grouped;
      } else {
        let sitems = value.result;

        var filtered = _.filter($rootScope.__suggests.contacts, i => { 
          return _.find(sitems, s => {
            return s.uid === i.uid;
          }) !== undefined;
        });

        this.groups = _.transform(_.groupBy(filtered, 'order'), (result, value, key) => {
          result.push({
            order: key,
            contacts: value
          });
        }, []);

        $timeout(() => {
          $scope.$apply();
        }, 200);
      }
    });

    $rootScope.$on('unselectAll', value => {

      _.each(_.flatten(_.map(_.values(this.groups), 'contacts')), i => { 
        console.log(i);
        i.$selected = false; 
        this.$rootScope.__removeContact(i);
      });
    });

    $rootScope.$on('selectAll', value => {
      _.each(_.flatten(_.map(_.values(this.groups), 'contacts')), i => { 
        i.$selected = true;
        this.$rootScope.__addContact(i); 
      });
    });
  }

  $onInit() {
    
  }

  itemClick($event, contact){
    console.log(contact);

    var parentEl = angular.element(document.body);
    this.$mdDialog.show({
         parent: parentEl,
         targetEvent: $event,
         template: require('./dialog-contact.html'),
         locals: {
           contact: contact
         },
         controller: DialogController
      });

    /*@ngInject*/
    function DialogController($scope, $mdDialog, contact) {
        $scope.contact = contact;
        $scope.closeDialog = function() {
          $mdDialog.hide();
        }
    }
  }

  addSingle(contact){
    contact.$working = true;
    this.$rootScope.__removeContact(contact);
    this.agendaService
      .add([contact])
      .then(data => {
        contact.$working = false;
        toastr.success(`Contacto(s) aadido(s)`);
        this.$rootScope.reload();
      })
      .catch(err => {
        console.log(err);
        toastr.error(err.statusText);
        contact.$working = false;
        this.$rootScope.reload();
      });
  }

  toggleContact(contact){
    if (contact.$selected !== true){
      this.$rootScope.__addContact(contact);
    } else {
      this.$rootScope.__removeContact(contact);
    }
  }
}

export default angular.module('abcContactsApp.sugeridos', [uiRouter])
  .config(routes)
  .component('sugeridos', {
    template: require('./sugeridos.html'),
    controller: SugeridosComponent
  })
  .name;



