import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './main.routes';
import toastr from 'toastr';
import _ from 'lodash';

export class MainController {

  /*@ngInject*/
  constructor($scope, $rootScope, $mdDialog, $state, $timeout) {

    if (!$rootScope._isLogin){
      return $state.go('auth', { from: 'main' });
    }

    this.$mdDialog = $mdDialog;

    $rootScope.$watch(() => { return $rootScope.__contacts; }, (value) => {
      if (value)
        this.groups = value.grouped;
    });

    $rootScope.$on('filterChange', () => {
      let value = $rootScope.__filter;
      
      if (!$rootScope.__contacts)
        return;

      if (!value) {
        this.groups = $rootScope.__contacts.grouped;
      } else {
        let sitems = value.result;

        var filtered = _.filter($rootScope.__contacts.contacts, i => { 
          return _.find(sitems, s => {
            return s.uid === i.uid;
          }) !== undefined;
        });

        this.groups = _.transform(_.groupBy(filtered, 'order'), (result, value, key) => {
          result.push({
            order: key,
            contacts: value
          });
        }, []);

        $timeout(() => {
          $scope.$apply();
        }, 200);
      }
    });

    $rootScope.$watch(() => { return $rootScope.__filter; }, (value) => {
      return;
      if (!$rootScope.__contacts)
        return;

      if (!value) {
        this.groups = $rootScope.__contacts.grouped;
      } else {
        let sitems = value.result;

        var filtered = _.filter($rootScope.__contacts.contacts, i => { 
          return _.find(sitems, s => {
            return s.uid === i.uid;
          }) !== undefined;
        });

        this.groups = _.transform(_.groupBy(filtered, 'order'), (result, value, key) => {
          result.push({
            order: key,
            contacts: value
          });
        }, []);

        $timeout(() => {
          $scope.$apply();
        }, 200);
      }
    });
  }

  $onInit() {
    
  }

  itemClick($event, contact){
    console.log(contact);

    var parentEl = angular.element(document.body);
    this.$mdDialog.show({
         parent: parentEl,
         targetEvent: $event,
         template: require('./dialog-contact.html'),
         locals: {
           contact: contact
         },
         controller: DialogController
      });

    /*@ngInject*/
    function DialogController($scope, $mdDialog, contact) {
        $scope.contact = contact;
        $scope.closeDialog = function() {
          $mdDialog.hide();
        }
    }
  }
}

export default angular.module('abcContactsApp.main', [uiRouter])
  .config(routing)
  .component('main', {
    template: require('./main.html'),
    controller: MainController
  })
  .name;



