'use strict';
/* eslint no-sync: 0 */

import angular from 'angular';
import toastr from 'toastr';
import QueryFilter from './filter';

export class NavbarComponent {
  menu = [{
    title: 'Home',
    state: 'main'
  }];
  isCollapsed = true;

  /*@ngInject*/
  constructor($rootScope, $state, $mdMedia, $mdSidenav, $mdUtil, agendaService, $scope, $http, $q){
    this.$mdSidenav = $mdSidenav;
    this.$rootScope = $rootScope;
    this.$mdMedia = $mdMedia;
    this.$state = $state;
    this.$scope = $scope;
    this.selectAll = false;
    this.toggleLeft = buildToggler('left');
    this.agendaService = agendaService;
    this.$http = $http;
    
    this.filter = new QueryFilter($q);
    this.isDisabled = false;
    this.noCache = true;
    this.selectedItem;
    this.searchText = '';
    this.items = [];

    this.me = { name: '',  avatar: ''};

    this.selectedsCount = 0;

    function buildToggler(navID) {
      var debounceFn =  $mdUtil.debounce(function(){
            $mdSidenav(navID)
              .toggle();
          },300);

      return debounceFn;
    }

    this.$rootScope.$watch(() => { return this.$rootScope.__hasSelected(); }, (value) => {
      this.hasSelected = value;
    });

    this.$rootScope.$watch(() => { return this.$rootScope.__selecteds.length; }, (value) => {
      this.selectedsCount = value;
    });

    this.$rootScope.$on('reload', () => {
      this.selectedItem = undefined;
      this.searchText = '';

      this.$http
        .post(`${this.$rootScope.__basePath}/api/me`)
        .then(res => {
          this.me = res.data;
        })
        .catch(err => {
          console.log(err);
          toastr.error(err.statusText);
        });
    });

    this.$scope.$watch(() => { return this.$scope.selectAll; }, value => {
      //console.log(value);
      //this.$rootScope.$emit('selectAll', value);
    });
  }

  toggleSelect(value){
    if (!value) {
      this.$rootScope.$emit('selectAll');
    } else {
      this.$rootScope.$emit('unselectAll');
    }
  }

  querySearch(text){
    
    let items = this.$rootScope.__contacts.contacts;
    if (this.$state.current.name==='sugeridos'){
      items = this.$rootScope.__suggests.contacts;
    }
    return this.filter.queryFilter(text, items);
  }

  selectedItemChange(item){
    this.$rootScope.__filter = item;
    //this.$rootScope.$apply();
    this.$rootScope.$emit('filterChange');
  }

  addSelectds(){
    this.$working = true;
    this.agendaService
      .add(this.$rootScope.__selecteds)
      .then(data => {
        this.$working = false;
        toastr.success(`Contacto(s) aadido(s)`);
        this.$rootScope.reload();
        this.$rootScope.__crealSelecteds();
        this.$rootScope.$emit('contactsAdded', {});
      })
      .catch(err => {
        console.log(err);
        toastr.error(err.statusText);
        this.$working = false;
        this.$rootScope.__crealSelecteds();
        this.$rootScope.$emit('contactsAdded', {});
      });
  }

  cancelSelectds() {
    this.$rootScope.__crealSelecteds();
    this.$rootScope.$emit('contactsAdded', {});
  }

  toggleSidenav(){
    //
    if (this.$mdMedia('gt-md')){
      this.$rootScope.__sidenavLeft.lock= !this.$rootScope.__sidenavLeft.lock;
    } else {
      this.toggleLeft();  
    }   
  }

}

export default angular.module('directives.navbar', [])
  .component('navbar', {
    template: require('./navbar.html'),
    controller: NavbarComponent
  })
  .name;



