'use strict';
const angular = require('angular');
import _ from 'lodash';
import async from 'async';
import $ from 'jquery';

/*@ngInject*/
export function agendaService($http, $q, $rootScope) {

	let cbasePath = $('base').attr('href');

  cbasePath = cbasePath || '/';

  if (cbasePath === '/'){
    cbasePath = '';
  }

  if (cbasePath.lastIndexOf('/') === cbasePath.length-1){
    cbasePath = cbasePath.substring(0, cbasePath.length-1);
  }

	var url = {
		myContacts: `${cbasePath}/api/agenda`,
		suggest: `${cbasePath}/api/agenda/suggest`,
		add:  `${cbasePath}/api/agenda/add`
	};

	function mapContacts(response){
		// procces the data
    let all = _.map(_.sortBy(response.data, 'givenName'), (c, i) => {
      c.order = _.upperCase(c.givenName[0].toString());
      c.$selected = _.find($rootScope.__selecteds, { uid: c.uid }) !== undefined;
      let avatar = c.avatar;
      //avatar = `http://lorempixel.com/36/36/people/${i}`;
      if (!avatar){
      	c.avatar = undefined;
      } else {
      	c.style = `{ 'background-image': 'url(${avatar})' }`;
      }
      return c;
    });

    let groups = _.transform(_.groupBy(all, 'order'), (result, value, key) => {
      result.push({
        order: key,
        contacts: value
      });
    }, []);

    return {
			count: all.length,
			grouped: groups,
			contacts: all
		};
	}

	var myContacts = () => {
		var def = $q.defer();
		$http
			.get(url.myContacts)
			.then(response => {
				def.resolve(mapContacts(response));
			})
			.catch(err => { def.reject(err); });
		return def.promise;
	};

	var suggest = () => {
		var def = $q.defer();
		$http
			.get(url.suggest)
			.then(response => {
				def.resolve(mapContacts(response));
			})
			.catch(err => { def.reject(err); });
		return def.promise;
	};

	var info = () => {
		var def = $q.defer();

		async.mapValues({
			'contact': myContacts,
			'suggest': suggest
		},
		(func, key, cb) => {
			func()
				.then(data => {
					cb(null, data.count);
				})
				.catch(cb);

		}, (err, results) => {
			if (err)
				return def.resolve({ contact: 0, suggest: 0 });
			def.resolve(results);
		});

		return def.promise;
	};

	var all = () => {
		var def = $q.defer();

		async.mapValues({
			'contact': myContacts,
			'suggest': suggest
		},
		(func, key, cb) => {
			func()
				.then(data => {
					cb(null, data);
				})
				.catch(cb);

		}, (err, results) => {
			if (err)
				return def.resolve({ contact: [], suggest: [] });
			def.resolve(results);
		});

		return def.promise;
	};

	var add = (contacts) => {
		var def = $q.defer();
		$http
			.post(url.add, contacts)
			.then(response => {
				def.resolve(response);
			})
			.catch(err => { def.reject(err); });
		return def.promise;
	};

	return {
		myContacts: myContacts,
		suggest: suggest,
		info: info,
		all: all,
		add: add
	};
}

export default angular.module('abcContactsApp.agenda', [])
  .service('agendaService', agendaService)
  .name;



