'use strict';

import _ from 'lodash';
import $ from 'jquery';

export function routeConfig($urlRouterProvider, $httpProvider, $locationProvider, $mdThemingProvider) {
  'ngInject';

  $urlRouterProvider.otherwise('/');

  $locationProvider.html5Mode(true);
  
  $httpProvider.interceptors.push('authInterceptor');

  $mdThemingProvider.definePalette('bagrey', {
  '50': '#e9eaea',
  '100': '#c2c3c5',
  '200': '#a6a7a9',
  '300': '#818486',
  '400': '#727477',
  '500': '#636567',
  '600': '#545657',
  '700': '#454648',
  '800': '#363738',
  '900': '#272829',
  'A100': '#e9eaea',
  'A200': '#c2c3c5',
  'A400': '#727477',
  'A700': '#454648',
  'contrastDefaultColor': 'light',
  'contrastDarkColors': '50 100 200 300 A100 A200'
  });

  $mdThemingProvider.definePalette('bagreen', {
  '50': '#eff7da',
  '100': '#d3e99c',
  '200': '#bfdf6e',
  '300': '#a5d233',
  '400': '#94bd2a',
  '500': '#80a424',
  '600': '#6c8b1e',
  '700': '#597219',
  '800': '#455913',
  '900': '#32400e',
  'A100': '#eff7da',
  'A200': '#d3e99c',
  'A400': '#94bd2a',
  'A700': '#597219',
  'contrastDefaultColor': 'light',
  'contrastDarkColors': '50 100 200 300 400 500 A100 A200 A400'
  });

  $mdThemingProvider.theme('default')
  .primaryPalette('bagreen')
  .accentPalette('blue');
}

export function appRun($rootScope, $state, agendaService, $timeout){
  'ngInject';

  $rootScope.__appVersion = $('meta[name="version"]').attr('content');
  
  let cbasePath = $('base').attr('href');

  cbasePath = cbasePath || '/';

  if (cbasePath === '/'){
    cbasePath = '';
  }

  if (cbasePath.lastIndexOf('/') === cbasePath.length-1){
    cbasePath = cbasePath.substring(0, cbasePath.length-1);
  }

  $rootScope.__basePath = cbasePath;

  $rootScope.reload = () => {
    $rootScope.__filter = undefined;
    $rootScope.__sidenavLeft.loading = true;
      agendaService
        .all()
        .then(data => {
          //console.log(data);
          $rootScope.__contacts = data.contact;
          $rootScope.__suggests = data.suggest;
          $rootScope.__sidenavLeft.countContact = data.contact.count;
          $rootScope.__sidenavLeft.countSeggest = data.suggest.count;
          $rootScope.__sidenavLeft.loading = false;
          //$rootScope.$apply();
        });
    $rootScope.$emit('reload');
  };

  $rootScope.__sidenavLeft = {
    lock: true,
    loading: true,
    countContact: 0,
    countSeggest: 0,
    reload: () => {
      $rootScope.reload();
    }
  };

  if ($rootScope._isLogin){
    $rootScope.reload();  
  }

  $rootScope.$on('login', () => {
    $rootScope._isLogin = true;
    $rootScope.reload();
  });

  $rootScope.$state = $state;

  $rootScope.__selecteds = [];

  $rootScope.__hasSelected = () => {
    return $rootScope.__selecteds.length > 0;
  };

  $rootScope.__crealSelecteds = () => {
    _.each($rootScope.__selecteds, s => { s.$selected = false; });
    $rootScope.__selecteds.splice(0, $rootScope.__selecteds.length);
  };

  $rootScope.__addContact = (contact) => {
    if (!_.find($rootScope.__selecteds, contact)){
      $rootScope.__selecteds.push(contact);
    }

    $timeout(() => {
      $rootScope.$apply();
    });
  };

  $rootScope.__removeContact = (contact) => {
    _.remove($rootScope.__selecteds, contact);
    console.log($rootScope.__selecteds);
  };
}

export function authInterceptor($rootScope, $q, $location){
  'ngInject';
  return {
    // Add authorization token to headers
    request: function (config) {
      config.headers = config.headers || {};
      if ($rootScope.__token) {
        config.headers.Authorization = 'Bearer ' + $rootScope.__token;
      }
      return config;
    },

    // Intercept 401s and redirect you to login
    responseError: function(response) {
      if(response.status === 401) {
        console.log($location.path);
        $location.path('/auth');
        // remove the token
        $rootScope.__token = undefined;
      }

      return $q.reject(response);
    }
  };
}


