'use strict';

import * as _ from "lodash";

export default class QueryFilter {
	constructor($q){
    this.$q = $q;
  }

  filterByName(search, originalText){
  	let reg = new RegExp(search, 'ig');

  	let query = function(item){
      let fullname = `${item.givenName} ${item.sn}`;
    	return reg.test(fullname) || reg.test(item.givenName) || reg.test(item.sn);
    };

    let res = _.filter(this.items, query);

    return _.map(res, i => {
    	return {
    		type: 'contacto',
    		caption: `${i.givenName} ${i.sn}`,
    		icon: 'fa-user',
    		result: [i]
    	};
    });
  }

  filterByClaveestab(search, originalText){
  	let reg = new RegExp(search, 'ig');

  	let items = _.groupBy(this.items, 'claveestab');

  	let query = function(item){
    	return reg.test(item);
    };

    let queryContacts = function(claveestab){
    	return function(item){
    		return item.claveestab === claveestab;
    	}
    };

    let res = _.filter(_.keys(items), query);

    return _.map(res, i => {
    	let result = _.filter(this.items, queryContacts(i));
    	return {
    		type: 'claveestab',
    		caption: `Claveestab ${i} (${result.length})`,
    		icon: 'fa-key',
    		result: result
    	};
    });

  }

  filterByCue(search, originalText){

  	let reg = new RegExp(search, 'ig');

  	let items = _.groupBy(this.items, 'institution.cue');

  	let query = function(item){
    	return reg.test(item);
    };

    let queryContacts = function(cue){
    	return function(item){
    		return item.institution.cue === cue;
    	}
    };

    let res = _.filter(_.keys(items), query);

    return _.map(res, i => {
    	let result = _.filter(this.items, queryContacts(i));
    	return {
    		type: 'cue',
    		caption: `CUE ${i} (${result.length})`,
    		icon: 'fa-institution',
    		result: result
    	};
    });

  }

  queryFilter(text, items){
  	var scaped = _.deburr(_.trim(text));
  	this.items = items;

    //let def = this.$q.defer();
    //return def.promise;

  	if (scaped === ''){
  		return [];
  	}

  	let res = [];

  	let fc = this.filterByName(scaped, text);

  	if (fc.length > 0)
  		res.push(fc);

  	let festab = this.filterByClaveestab(scaped, text);

  	if (festab.length > 0)
  		res.push(festab);

  	let fcue = this.filterByCue(scaped, text);

  	if (fcue.length > 0)
  		res.push(fcue);

  	return _.flatten(res);
  }
}


