'use strict';
/* eslint no-sync: 0 */

import angular from 'angular';

export class SidenavComponent {
  isCollapsed = true;
}

export default angular.module('directives.sidenav', [])
  .component('sidenav', {
    template: require('./sidenav.html'),
    controller: SidenavComponent
  })
  .name;



