'use strict';

import angular from 'angular';
import ngAnimate from 'angular-animate';
import ngCookies from 'angular-cookies';
import ngResource from 'angular-resource';
import ngSanitize from 'angular-sanitize';

import uiRouter from 'angular-ui-router';
import ngLoader from 'angular-loading-bar';
import ngMaterial from 'angular-material';

import {
  routeConfig,
  appRun,
  authInterceptor
} from './app.config';

import navbar from '../components/navbar/navbar.component';
import sidenav from '../components/sidenav/sidenav.component';
import footer from '../components/footer/footer.component';
import constants from './app.constants';
import util from '../components/util/util.module';

// routes
import main from './main/main.component';
import sugeridos from './sugeridos/sugeridos.component';
import auth from './auth/auth.component';
import test from './test/test.component';
import logout from './logout/logout.component';

// services
import agendaService from './services/agenda.service';

// filters
import contactFilter from './filters/contact.filter';
import groupFilter from './filters/group.filter';

import 'toastr/build/toastr.css';
import 'angular-material/angular-material.css';
import 'angular-loading-bar/build/loading-bar.css';
import './app.scss';

angular.module('abcContactsApp', [
	//ngAnimate,
	ngCookies, 
	ngResource, 
	ngSanitize, 
	uiRouter, 
	ngLoader,
	ngMaterial,
	navbar, 
	sidenav,
	footer, 
	main,
	auth,
	test,
	logout,
	sugeridos,
	agendaService,
	contactFilter,
	groupFilter,
  constants, 
  util
])
.factory('authInterceptor', authInterceptor)
.config(routeConfig)
.run(appRun);

angular.element(document)
  .ready(() => {
    angular.bootstrap(document, ['abcContactsApp'], {
      strictDi: true
    });
  });



