import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './auth.routes';
import toastr from 'toastr';
import _ from 'lodash';

export class AuthController {

  /*@ngInject*/
  constructor($rootScope, $state, $http, $stateParams) {
    this.$http = $http;
    this.$rootScope = $rootScope;
    this.$stateParams = $stateParams;
    this.$state = $state;

    this._from = 'main';

    if ($stateParams.from !== undefined){
      this._from = $stateParams.from;
    }
  }

  $onInit() {
    this.$http
        .post(`${this.$rootScope.__basePath}/api/auth`)
        .then( res => {
          this.$rootScope.__token = res.data.token;
          this.$rootScope._isLogin = true;
          this.$rootScope.$emit('login');
          this.$state.go(this._from);
        })
        .catch(err => {
          console.log(err);
          toastr.error(err.statusText);
        });
  }
}

export default angular.module('abcContactsApp.auth', [uiRouter])
  .config(routing)
  .component('auth', {
    template: require('./auth.html'),
    controller: AuthController
  })
  .name;



